
div{
    z-index:1;
}
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: transparent; 
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #8cd1f1; 
    border-radius: 10px;
}
::-webkit-scrollbar:hover{
   width: 10px;
}
::selection {
  background: rgb(0, 204, 255, 0.5);
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    width: 10px;
    background: #8cd1f1;
}
body{
    padding: 0px;
    margin: 0px;
    overflow-x: hidden;
    position: relative;
}
.dropdown .drop-downs-items{
    display: none
}
.main_message_popper{
    position: absolute;
    width: 100%;
    display: block;
    left: 0px;
    background-color: transparent;
    border-radius: 3px;
    overflow: hidden;
    z-index: 99!important;
}
.main_message_popper .relative{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.main_message_popper .relative .message{
   background-color: aquamarine;
   padding: 10px;
   border-radius: 10px;
   min-width: 270px;
}
.main_message_popper .relative .message.error{
    background-color: rgb(138, 69, 69);
}

.container {
    width: 800px;
    margin: 10px 200px;
    position: relative;
}
textarea{
    /* margin-top: 30px; */
    width: 600px;
    height: 100%;
}
@media (width<=800px){
    .container{
        width:100vw;
    }
    textarea{
    /* margin-top: 30px; */
        max-width: 600px;
        min-width:auto;
        width:auto;
    }
    .other-toolsholders input,.other-toolsholders textarea {
        width:60%!important;
        height:20px!important;
        font-size:12px!important;
    }
    .other-toolsholders{
        margin:0px!important;
        margin-top:10px!important;
        margin-bottom:10px!important;
        padding:0px!important;
    }
    .other-toolsholders button{
        padding:5px!important;
        font-size:13px!important;
        font-weight:600!important;
    }
    #timeShower{
        min-width: 45px!important;
        min-height: 15px!important;
    }
}
#text_to_show {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
    border: 2px solid rgb(231, 228, 228);
    box-sizing: border-box;
    height: 80px;
    top: -100px;
    margin-top: 20px;
    padding: 10px;
    display: inline-block;
    overflow-wrap: break-word;
    /* -ms-word-break: break-word; */
    overflow: hidden;
    box-shadow: 10px 10px 10px 20px solid black;
    border: 1px dashed black;
    font-size: calc(0.8vw + 0.8em);
    direction: ltr;
    margin: auto;
    text-align: justify;
    word-break: break-word;
    /* white-space: pre-line; */
    overflow-wrap: break-word;
    -ms-word-break: break-word;
    word-break: break-word;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}
@media (width<=1000px) {
    .container{
        margin-left: 20px;
        width: calc(100vw - 40px);

    }
    #text_to_show{
        font-size: calc(1.5vw + 0.8em);
    }

}
@media (width<=600px) {
    .container{
        margin-left: 20px;
        width: calc(100vw - 40px);

    }
    #text_to_show{
        font-size: calc(2vw + 0.4em);
        font-weight: 700;
    
    }

}
#text_to_show .wraper-text_write {
    font-weight: 500;
    padding: 3px 5px 3px 5px;
    text-transform: lowercase;
}

#text_to_show .progress {
    background-color: lightgray;
}

#text_to_show .warning {
    background-color: red;
}

#text_to_show .success {
    color: green;
}

#text_to_show .wrong {
    color: red;
}

.other-toolsholders {
    margin: 10px 5%;
    width: 100%;
    display: flex;
    align-items: center;
}

.other-toolsholders input,.other-toolsholders textarea {
    width: 600px;
    height: 30px;
    font-size: 20px;
}

.other-toolsholders button {
    padding: 10px;
    background-color: rgb(16, 167, 236);
    font-weight: 600;
    font-size: 20px;
    color: white;
    cursor: pointer;
    border: 1px solid lightgray;
    border-radius: 5px;
}
#timeShower{
    min-width: 65px;
    min-height: 45px;
}
.other-toolsholders button:hover{
    background-color: green;
}
#resultFreme {
    border: 2px solid skyblue;
    background-color: rgb(255, 255, 255);
    display: none;
    width: 300px;
    margin: 0px 20%;
    border-radius: 15px;
    font-family: Arial, Helvetica, sans-serif;
    /* padding-bottom: 5px; */
}
@media (width<=800px){
    #resultFreme{
        width: 98vw!important;
        margin: 0px!important;
    }
    .result_frame {
    
     width: 100%!important; 
     margin: 0px!important; 
    }
}
#resultFreme:nth-child(even){
    background-color: rgb(245, 245, 245);
}
#resultFreme:last-child{
    background-color: rgb(245, 245, 245);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
#resultFreme .result_header {
    background-color: rgb(11, 137, 187);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin: 0px;
    display: flex;
    color: white;
    border-bottom: 1px solid lightgray;
    padding: 20px;
}

#resultFreme .result_header .res {
    margin: 0px 20px;
    font-size: 20px;
    font-weight: 600;

}

#resultFreme .result_header .screen_shot {
    margin: 0px 20px;
    font-size: 20px;
    font-weight: 500;
    text-decoration: none;
    color: white;

}

#resultFreme .result {
    border-bottom: 1px solid lightgray;
    background-color: rgb(245, 245, 245);
    position: relative;
    padding: 20px 40px;
    color: green;
    font-size: 40px;
    font-weight: 800;
}

#resultFreme .result .wpmdef {
    font-size: 15px;
    font-weight: 400;
    color: black;
    position: absolute;
    bottom: 10px;
    left: 90px;
}

#resultFreme .stokes {
    border-bottom: 1px solid lightgray;
    position: relative;
    padding: 20px;
}
#resultFreme .media-share{
    margin:10px;
}
#resultFreme .stokes .right {
    color: green;
}

#resultFreme .stokes .wrong {
    color: red;
}

#resultFreme .data-rows {
    color: black;
    position: absolute;
    right: 10px;
    font-weight: 600;
}

#resultFreme .c_words {
    border-bottom: 1px solid lightgray;
    position: relative;
    padding: 20px;
}

#resultFreme .accuracy {
    border-bottom: 1px solid lightgray;
    background-color: rgb(245, 245, 245);
    position: relative;
    padding: 20px;
}

#resultFreme .w_words {
    border-bottom: 1px solid lightgray;
    position: relative;
    padding: 20px;
}

#resultFreme .c_words .data-rows {
    color: green;

}

#resultFreme .w_words .data-rows {
    color: red;

}

