.menu-box-for-tools{
    position: fixed;
    z-index: 99999;
    min-width: 200px;
    overflow: hidden;
    border: 2px solid light;
    background: #7a7474a6;
    box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.479);
    -webkit-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.397);
    -moz-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.288);
    z-index: 9998989898;
    max-height: 600px;
    border-radius: 5px;
    color: rgb(0, 217, 255)!important;
    
}
.menu-box-for-tools .filter-bar {
    margin-top: 10px;
    padding-bottom: 0px;
    margin-bottom: 0px;
}
.menu-box-for-tools .inner_menu{
    overflow-y: scroll;
    max-height: 370px;
}
.menu-box-for-tools button{
    font-size: 17px;
    color: white;
    font-weight: 600;
    position: relative;
    display: flex;
    align-items: center;
    padding: 5px;
    margin: 0px;
    padding-right: 30px;
    border: none;
    background-color: transparent;
    width: 100%;
    min-height: 30px;
    text-align: left;
}
.menu-box-for-tools button .icon svg{
    height: 20px;
    width: 20px;
}
.menu-box-for-tools button .icon img{
    height: 20px;
    width: 20px;
}
.menu-box-for-tools button.checkable{
    padding-left: 40px;
}
.menu-box-for-tools button .rf{
    position: absolute;
    top: auto;
    right: 12px;
}
.menu-box-for-tools button .lf{
    position: absolute;
    top: auto;
    left: 12px;
}
.menu-box-for-tools button input{
    outline: 0;
    border:none;
    font-size: 17px;
    font-weight: 500;
    background-color: transparent;
    border-bottom: 2px solid #76b3cf;
}
.menu-box-for-tools button input:focus{
    border-bottom: 2px solid blue;
}
.menu-box-for-tools button button,.menu-box-for-tools button button:focus{
    width: 30px;
    height: 30px;
    background-color: transparent;    
}
.menu-box-for-tools button button:hover{
    background-color: rgb(167, 250, 250);
    border-radius: 50%;
}
.menu-box-for-tools button:hover,.menu-box-for-tools button:focus, .menu-box-for-tools button.focused,.formatter-frame  .tool-button:hover,.formatter-frame  .tool-button:focus, .formatter-frame  .tool-button.focussed{
    background-color: rgb(90, 236, 236);
    cursor: pointer;
    outline: 0;
    color: white;
    
}
.tool-tip-frame{
    position: fixed;
    z-index: 99999;
    background-color: black;
    color: white;
    border-radius: 10px;
    font-size: 20px;
    padding: 10px;
}
.tool-tip-frame{
    position: fixed;
    z-index: 99999;
    background-color: black;
    color: white;
    border-radius: 10px;
    font-size: 20px;
    padding: 10px;
}
.menu-box-for-tools button .wrapper{
    display: flex;
}
#durationChanger{
    display: flex;
    align-items: center;
